export default {
	name: '',
	props: {
		item: Object,
	},	
	data () {
		return {
			list: {}
		}
	},
	watch: {
		item () {
			this.list = this.item;
			if(this.list.price.includes('-')) {
				this.$set(this.list, 'withdraw', 'true')
				this.$set(this.list, 'price', this.list.price.replace('-',''))
			}
		}
	},
	onLoad () {

	},
	methods: {

	}
}
